html {
  scroll-behavior: smooth;
}

body {
  background-color: #e2e2e2;
  color: #3a3a3a;
  /* width: 100%;
  min-width: 1000px; */
}

#aboutme {
  display: flex;
  background-image: url("./images/yosemite.jpg");

  min-height: 100vh;
  
  background-attachment: fixed;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}

#aboutme-text {
  width: 30rem;
  margin: auto auto auto 2rem;
  color: black;
  background-color: #e2e2e2de;
  padding: 0 2rem 2rem 2rem;
}

.MuiAvatar-root {
  margin: auto 0 auto auto;
}

@media (max-width: 812px) {
  #aboutme {
    display: grid;
  }
  
  #aboutme-text {
    width: 80%;
    margin:0 auto auto auto;
  }

  .MuiAvatar-root {
    margin: auto auto 0 auto;
  }

}



#project-header {
  text-align: center;
  font-size: 3rem;
  line-height: 10rem;
  height: 10rem;
  background-color: #c2c2c2;
  letter-spacing: 5px;
  box-shadow: 0 0 10px;
  border: 1px solid #bbbbbb;
}

#projects {
  display: flex;
  background-color: #ebebeb;
}

.MuiList-root {
  width: 300px;
  min-width: 300px;
  background-color: #d1d1d1;
  height: 40rem;
  max-height: 40rem;
  color: black;
  opacity: 0.92;
}

#dropdown {
  background-color: #a5a5a5;
  position: absolute;
  z-index: 1;
  right:-1.5rem;
  box-shadow: 0 0 3px;
  top: 50%;
}

#project-img-preview {
  height: 27rem;
  width: 32rem;
  object-fit: cover;
  border-radius: 20px;
  box-shadow: 0 0 10px;
  margin: auto auto auto 5rem;
}

